@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .sector {
    @apply absolute w-diameter-sm h-diameter-sm md:w-diameter-md md:h-diameter-md xl:w-diameter-xl xl:h-diameter-xl left-1/2 top-1/2 -mt-diameter-sm md:-mt-diameter-md xl:-mt-diameter-xl -ml-diameter-sm md:-ml-diameter-md xl:-ml-diameter-xl border border-white origin-[100%_100%] flex items-end justify-end;
  }

  .sector-label {
    @apply text-white w-radius-sm md:w-radius-md xl:w-radius-xl ml-auto text-center;
  }
}
